/*=====================
   2.6 Form CSS start
==========================*/
.form-control, .form-select {
  background-color: #2B2B2B;
  color: #EFEFEF;
}
.form-control:focus, .form-select:focus {
  background-color: #2B2B2B;
  border-color: #ced4da;
  color: #EFEFEF;
}

.form-floating > .form-control {
  height: 42px;
}

.radio_animated {
  position: relative;
  margin: -2px 1rem 0 1px;
  cursor: pointer;
}
[dir=rtl] .radio_animated {
  margin: -2px 1px 0 1rem;
}
.radio_animated:before {
  transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
  transform: scale(0, 0);
  content: "";
  position: absolute;
  top: 4px;
  left: 3px;
  z-index: 1;
  width: 0.55rem;
  height: 0.55rem;
  background: #FF4C3B;
  border-radius: 50%;
}
[dir=rtl] .radio_animated:before {
  left: unset;
  right: 3px;
}
.radio_animated:after {
  content: "";
  position: absolute;
  top: 0;
  left: -1px;
  width: 1.05rem;
  height: 1.05rem;
  background: #2B2B2B;
  border: 1px solid #dddddd;
  border-radius: 50%;
}
[dir=rtl] .radio_animated:after {
  left: unset;
  right: -1px;
}
.radio_animated:checked:before {
  transform: scale(1, 1);
}
.radio_animated:checked:after {
  border: 1px solid #FF4C3B;
}

.checkbox_animated {
  padding-left: 0;
}
[dir=rtl] .checkbox_animated {
  padding-right: 0;
}
.checkbox_animated input[type=checkbox] {
  display: none;
}
.checkbox_animated input[type=checkbox] + label {
  display: flex;
  align-items: center;
  color: #BABABA;
  cursor: pointer;
}
.checkbox_animated input[type=checkbox] + label:before {
  content: "";
  border: 1px solid #c2c2c2;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  padding-left: 0;
  padding-bottom: 0;
  margin-right: 10px;
  vertical-align: bottom;
  color: transparent;
  transition: 0.2s;
  line-height: 1;
}
[dir=rtl] .checkbox_animated input[type=checkbox] + label:before {
  padding-right: 0;
  margin-right: 0;
  margin-left: 10px;
}
.checkbox_animated input[type=checkbox] + label:active:before {
  transform: scale(0);
}
.checkbox_animated input[type=checkbox]:checked + label:before {
  background-color: #FF4C3B;
  border-color: #FF4C3B;
  color: #fff;
}
.checkbox_animated input[type=checkbox]:checked:disabled _ label:before {
  transform: scale(1);
  background-color: rgba(255, 76, 59, 0.2);
  border-color: rgba(255, 76, 59, 0.2);
}
.checkbox_animated input[type=checkbox]:disabled _ label:before {
  transform: scale(1);
  border-color: #aaa;
}

.form-theme {
  background-color: #232323;
  border: none;
  font-size: 16px;
  color: #BABABA;
}
.form-theme:focus {
  background-color: #232323;
}

.form-floating > label {
  padding: 2px 5px;
  background-color: #2B2B2B;
  height: auto;
  left: 16px;
  top: -12px;
  color: #BABABA;
  text-transform: capitalize;
}
.form-floating > .form-select,
.form-floating > .form-control {
  padding: 5px 15px !important;
  height: 42px;
}

.form-floating > .form-control:focus ~ label,
.form-floating > .form-control:not(:placeholder-shown) ~ label,
.form-floating > .form-select ~ label {
  opacity: 1;
  transform: none;
}

.form-select:focus {
  box-shadow: none;
  border-color: #ced4da;
}

/*=====================
   2.7 Loader CSS start
==========================*/
.loader {
  display: flex;
  padding: 0px;
  text-align: left;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #2B2B2B;
  z-index: 9;
  align-items: center;
  justify-content: center;
}
.loader span {
  position: absolute;
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background: #FF4C3B;
  animation: loader 1.5s linear infinite;
}
.loader span:last-child {
  animation-delay: -0.9s;
}

@keyframes loader {
  0% {
    transform: scale(0, 0);
    opacity: 0.8;
  }
  100% {
    transform: scale(1, 1);
    opacity: 0;
  }
}
